.modal {
  background-color: #fff;
  background: #555;
  display: none;
  height: auto;
  left: 50%;
  position: fixed;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 550px;
  z-index: 1001;
  &__close {
    // @include sprite(Cancel);
    border: none;
    background-color: transparent;
    cursor: pointer;
    margin-top: 3px;
    font-size: 16px;
  }
  &__content {
    padding: 0 75px;
  }
  &-backdrop {
    position: fixed;
    left: 0;
    top: 0;
    opacity: 0;
    background: rgba(19,29,23,.64);
    width: 100%;
    height: 100%;
    z-index: 1000;
    -webkit-transition: opacity .5s ease;
    -o-transition: opacity .5s ease;
    transition: opacity .5s ease;
  }
}